@import url(https://fonts.googleapis.com/css?family=Roboto);
#layout-div {
    display: grid;
    grid-template-areas: 
        ". header ."
        ". top ." 
        "stripe-background stripe-background stripe-background"
        ". main ."
        ". bottom ."
        ". info ."
        ". footer footer-right";
    grid-template-columns: auto 950px auto;
    grid-template-rows: auto auto var(--shorter-length) auto auto auto auto;
    grid-column-gap: var(--short-length);
    width: 100%;
}

.grey-gradient-background {
    background: -webkit-radial-gradient(var(--grey-gradient)) ;
    background: radial-gradient(var(--grey-gradient)) ;
    grid-column: 1 / 4;
    grid-row: 1 / 3;
}

.grey-dark-background {
    background: var(--dark-grey);
    grid-column: 1 / 4;
    grid-row: 6 / 7;
}

.grey-darker-background {
    background: var(--darker-grey);
    grid-column: 1 / 4;
    grid-row: 7 / 8;
}

.stripe-background {
    background-image: -webkit-linear-gradient(315deg, #cccccc 12.50%, #efefef 12.50%, #efefef 50%, #cccccc 50%, #cccccc 62.50%, #efefef 62.50%, #efefef 100%);
    background-image: linear-gradient(135deg, #cccccc 12.50%, #efefef 12.50%, #efefef 50%, #cccccc 50%, #cccccc 62.50%, #efefef 62.50%, #efefef 100%);
    background-size: 5.66px 5.66px; 
    grid-area: stripe-background;
}
header {
    align-items: center;
    display: flex;
    grid-area: header;
    height: 28rem;
    justify-content: flex-start;
}

header div {
    flex-shrink: 0;
    padding: 0 1rem;
}

.header-image-div {
    width: 40rem;
}

header img {
    height: 21rem;
    padding: 0 3rem;
}

header em {
    color: white;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -o-font-smoothing: antialiased;
}

header p {
    color: var(--lighter-grey);
    margin: 2rem 0;
    padding: 0;
}
.blue-button {
    color: white;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -o-font-smoothing: antialiased;
    background: -webkit-gradient(linear, left top, left bottom, from(var(--blue-gradient)));
    background: -webkit-linear-gradient(var(--blue-gradient));
    background: linear-gradient(var(--blue-gradient));
    border-radius: 5px;
    font-size: 1rem;
    font-weight: 500;
    padding: 0.5rem 1rem;
    text-transform: uppercase;
}
#top-div {
    align-items: center;
    background: var(--blue);
    border-radius: 2px 2px 0 0;
    display: flex;
    grid-area: top;
    height: var(--longer-length);
    justify-content: space-between;
    line-height: var(--longer-length);
    padding-left: 50px;
    padding-right: 2rem;
    position: relative;
}

#top:after {
    border-right: 3.5rem solid transparent;
    border-top: 3.5rem solid var(--blue);
    bottom: -3.5rem;
    content:"";
    height: 0;
    left:0;
    position: absolute;
    width: 0;
}
.white-button {
    color: var(--dark-grey);
    background: -webkit-gradient(linear, left top, left bottom, from(var(--white-gradient)));
    background: -webkit-linear-gradient(var(--white-gradient));
    background: linear-gradient(var(--white-gradient));
    border-radius: 5px;
    font-size: 1rem;
    font-weight: 500;
    height: var(--medium-length);
    line-height: var(--medium-length);
    padding: 0 3rem 0;
    text-transform: uppercase;
}
main {
    display: grid;
    grid-area: main;
    grid-template-areas: 
        "mainA"
        "mainB"
        "mainC";
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    width: 100%;
}

#mainA {
    grid-area: mainA;
    margin-top: calc(var(--longer-length) - var(--shorter-length));
}

.content1 {
    display: grid;
    grid-column-gap: var(--short-length);
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
}

#mainB {
    grid-area: mainB;
    margin-top: var(--longer-length);
}

#mainC {
    display: grid;
    grid-area: mainC;
    grid-column-gap: var(--short-length);
    grid-template-areas: 
        "content2 content2 content2"
        "title3 title3 title4"
        "content3 content3 content4";
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto auto auto;
}

.content2 {
    display: grid;
    grid-area: content2;
    grid-column-gap: var(--short-length);
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto;
    margin-bottom: var(--longer-length);
    margin-top: var(--medium-length);
    width: 100%;
}

.title3 {
    grid-area: title3;
}

.content3 {
    display: grid;
    grid-area: content3;
    grid-column-gap: var(--short-length);
    grid-row-gap: calc(3*var(--short-length));
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin-top: var(--medium-length);
    width: 100%;
}

.title4 {
    grid-area: title4;
}

.content4 {    
    grid-area: content4;
    height: 100%;
    margin-top: var(--medium-length);
    height: calc(100% - var(--medium-length));
}
.feature-div {
    display: grid;
    grid-row-gap: var(--short-length);
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 0.25fr 1fr 0.5fr;
    justify-items: center;
    padding: var(--short-length);
    position: relative;
}

.feature-div p {
    text-align: center;
}

.feature-div:hover {
    background-color: white;
    box-shadow: 2rem 0 8rem -4rem; 
}

.feature-div:hover::after {
    border-right: 2.5rem solid transparent;
    border-top: 2.5rem solid white;
    bottom: -2.5rem;
    content:"";
    height: 0;
    left:0;
    position: absolute;
    width: 0;
}

.circle-div {
    background: -webkit-radial-gradient(var(--grey-gradient));
    background: radial-gradient(var(--grey-gradient));
    border-radius: 50%;
    height: 80px;
    width: 80px;
    text-align: center;
}

.feature-div:hover .circle-div {
    background: -webkit-radial-gradient(var(--blue-gradient));
    background: radial-gradient(var(--blue-gradient));
}

.feature-div i {
    color: white; 
    font-size: 2rem;
    line-height: 80px;
}

.feature-div:hover a {
    background: transparent;
    border: none;
    color: var(--blue);
    font-size: 1rem;
    font-weight: 600;
}
.read-more-button {    
    background: -webkit-gradient(linear, left top, left bottom, from(var(--blue-gradient)));    
    background: -webkit-linear-gradient(var(--blue-gradient));    
    background: linear-gradient(var(--blue-gradient));
    border-radius: 5px;
    color: white;
    font-size: 0.9rem;
    font-weight: 500;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -o-font-smoothing: antialiased;
    padding: 1rem;
    height: var(--medium-length);
}
.blog-div {
    background: white;
    box-shadow: 2rem 0 8rem -4rem; 
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    position: relative;
}

.blog-div::after {
    border-right: 2.5rem solid transparent;
    border-top: 2.5rem solid white;
    bottom: -2.5rem;
    content:"";
    height: 0;
    left:0;
    position: absolute;
    width: 0;
    z-index: 10;
}

.blog-thumbnail-div {
    background: var(--grey);
    height: 12rem;
    width: 100%;
}

.blog-snippet-div {
    padding: var(--short-length); 
}
.testimonial-div {
    background: white;
    padding: var(--short-length);
    position: relative;
    box-shadow: 1rem 0 4rem -2rem; 
}

.testimonial::after {
    border-right: 2.5rem solid transparent;
    border-top: 2.5rem solid white;
    bottom: -2.5rem;
    content:"";
    height: 0;
    left:0;
    position: absolute;
    width: 0;
    z-index: 10;
}

.testimonial p {
    font-style: italic;
}
.title-div {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    grid-template-rows: var(--shorter-length);
    width: 100%;
}

.stripes {
    background-image: -webkit-linear-gradient(315deg, #cccccc 12.50%, #efefef 12.50%, #efefef 50%, #cccccc 50%, #cccccc 62.50%, #efefef 62.50%, #efefef 100%);
    background-image: linear-gradient(135deg, #cccccc 12.50%, #efefef 12.50%, #efefef 50%, #cccccc 50%, #cccccc 62.50%, #efefef 62.50%, #efefef 100%);
    background-size: 5.66px 5.66px;
}
.tabs
{
    position: relative;
    clear: both;
    height: 100%;
}

.content {
    margin-top: var(--short-length);
    padding: var(--short-length);
    width: 100%;
    height: calc(100% - 2.66*var(--short-length)); /* not happy with this eye-balled solution */
    position: absolute;
    left: 0px;
    top: 30px;
}

.tab {
    float: left;
    height: 100%;
}

.checkboxtab {
	display: none;
}

.tab label {
    padding: var(--short-length);
    background-color: #ffffff88;
    position: relative;
    top: var(--short-length);
}

.checkboxtab:checked ~ label {
    background: white;
    border-bottom: 1px solid white;
    box-shadow: 0.5rem 0 2rem -1rem; 
}

.checkboxtab:checked ~ .content {
    background: white;
    box-shadow: 2rem 0 8rem -4rem; 
	z-index: 1;
}
#bottom-div {
    align-items: center;
    background: var(--blue);
    border-radius: 2px 2px 0 0;
    display: flex;
    grid-column: 2 / 3;
    grid-row: 5 / 6;
    justify-content: space-between;
    font-size: 1.452rem;
    height: var(--long-length);
    line-height: var(--long-length);
    margin-top: var(--longer-length);
    padding-left: 55px;
    padding-right: 1rem;
    position: relative;
}

#bottom-div::after {
    border-right: 3rem solid transparent;
    border-top: 3rem solid var(--blue);
    bottom: -3rem;
    content: "";
    height: 0;
    left: 0;
    position: absolute;
    width: 0;
}
#info-div {
    display: grid;
    color: var(--light-grey);
    grid-area: info;
    grid-column-gap: var(--short-length);
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    padding-bottom: var(--short-length);
    padding-top: var(--long-length);
}

#info-div h5 {
    color: white;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -o-font-smoothing: antialiased;
    margin-bottom: var(--short-length);
}

#info-div hr {
    border: 1px solid var(--darker-grey);
    margin: 0.6rem 0;
}

.flickr-image-grid {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat (4, autofill);
    grid-template-rows: 1fr 1fr;
    grid-gap: 7px;
    width: 100%
}
.tweet-p > b {
    color: white;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -o-font-smoothing: antialiased;
}
.blog-link {
    color: var(--lighter-grey);
}

.blog-link:hover {
    color: var(--blue);
}
.flickr-image-div {
    background-color:var(--darkest-grey);
    height: 50px;
    width: 50px;
}
footer {
    align-items: center;
    color: var(--light-grey);
    display: flex;
    flex-flow: row nowrap;
    grid-area: footer;
    height: var(--longer-length);
    justify-content: space-between;
}

footer div {
    width: auto;
}

footer p {
    line-height: var(--long-length);
    margin: 0;
    padding:0;
}

.social-media-div {
    height: auto;
    margin-bottom: var(--short-length);
    margin-top: var(--short-length);
}

.fab {
    color: var(--darkest-grey);
    font-size: 2.5rem;
    margin-left: calc(0.5*var(--short-length));
}

.fab:hover, .fas:hover {
    color: var(--lightest-grey);
}
.social-media-a .fab {
    color: var(--darkest-grey);
    font-size: 2.5rem;
    margin-left: calc(0.5*var(--short-length));
}

.social-media-a .fab:hover {
    color: var(--lightest-grey);
}
* {
    box-sizing: border-box;
    font-family: sans-serif;
    font-weight: normal;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    text-decoration: none;
}

:root {
    --shorter-length: 0.85rem;
    --short-length: 1.4rem;
    --medium-length: 3rem;
    --long-length: 4.5rem;
    --longer-length: 6rem;
    --white-gradient: white, #EFEFEF;
    --grey-gradient: #666666, #252525;
    --blue-gradient: #458ED3, #387bb9;
    --lightest-grey: #EFEFEF;
    --lighter-grey: #CCCCCC;
    --light-grey: #aaaaaa;
    --grey: #6C6C6C;
    --dark-grey: #404040;
    --darker-grey: #353535;
    --darkest-grey: #252525;
    --blue: #458ED3;
}

html {
    background: #EFEFEF;
    background: var(--lightest-grey);
    color: #353535;
    color: var(--darker-grey);
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
    font-weight: 300;
    line-height: 1.25;
    width: 100%;
}

a {
    color: #353535;
    color: var(--darker-grey);
}

h1 {
    color: #aaaaaa;
    color: var(--light-grey);
    font-size: 1.5972rem;
    font-weight: 100;
    text-transform: uppercase;
}

h2 {
    color: white;
    font-size: 1.452rem;
    font-weight: 100;
    text-transform: uppercase;
}

h3 {
    font-size: 1.21rem;
    font-weight: 100;
    text-transform: uppercase;
}

h4 {
    align-self: top;
    color: #6C6C6C;
    color: var(--grey);
    font-size: 1.1rem;
    font-weight: 400;
    line-height: 0.8rem;
    padding: 0 2rem;
    text-align: center;
    text-transform: uppercase;
}

h5 {
    font-size: 1rem;
    font-weight: 900;
    margin-bottom: 0.3rem;
}

em {
    font-style: italic;
    font-weight: 900;
}

b {
    font-weight: 900; 
}

p {
    padding-bottom: 1.4rem;
    padding-bottom: var(--short-length);
}

.smaller-italic {
    font-size: 0.9rem;
    font-style: italic;
}
